body {
  box-sizing: border-box;
  margin: 0;
  padding: 0%;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

.spanHome {
  border: black solid 1px;
  padding: 5px;
  background-color: white;
  color: black;
}

.mid {
  border: black solid 1px;
  background-color: red;
  border-top: initial;
}

img {
  height: 150px;
  width: 150px;
}

p {
  align-content: inherit;
  color: white;
}

img.extend {
  height: 250px;
  width: 500px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.scrolling {
  border: black dotted 1px;
  background-color: white;
}

marquee {
  background-color: white;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  direction: ltr;
}

b {
  background-color: white;
}

.menu {
  border: white dotted 1px;
  color: white;
}

link {
  color: white;
  background-color: white;
}

.signin {
  background-color: white;
}
